import 'bootstrap/css/bootstrap.css'
import React from 'react';
import ReactDOM from 'react-dom';
import Button from 'react-bootstrap/lib/Button';
import CustomNav from './NavbarComp.js';
import StatusBar from './StatusBar.js'
import OptionsBar from './OptionsBar.js'
import GraphPanel from './GraphPanel.js'
import './index.css';
// import LeftOptions from './App.js'


var selectedOption = [];


class Portal extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedOptions: selectedOption,
      currentChoice: []
    };
  }

// Option Bar Functions -----------------------------------
// NOTE: react-select component returns object {label, value}, must select either/or
  handleChange(value) {
    this.state.currentChoice.push(value.label)
    this.setState({
      currentChoice: this.state.currentChoice});
  }

// Remove gene from list
  removeGene (itemIndex) {
    this.state.currentChoice.splice(itemIndex, 1);
    this.setState({
      currentChoice: this.state.currentChoice});
  }



// Render Space ------------------------------------------
  render() {

    const choices = this.state.currentChoice;
    const genes = choices.map((gene, index) => {
      return(
        <li key={index} >
          <Button bsStyle="success">
            {gene}
          </Button>
        </li>
        );
    });

    return (
      <div>
      <CustomNav />
      <div className="game">
        <div className="options-bar">
          <OptionsBar 
            onChange={(value) => this.handleChange(value)}
            geneChoices={choices}
            removeGene={(gene) => this.removeGene(gene)}
            geness={genes}
          />
        </div>
        <div className="graph-panel">
        <GraphPanel />
        </div>
        <div className="status-bar">
          <StatusBar />
        </div>
      </div>
      </div>
    );
  }
}
//


// ========================================

ReactDOM.render(
  <Portal />,
  document.getElementById('root')
)



